.message {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 30px;
}

.imageAndText {
  display: flex;
}

.imageAndText img {
  order: 2;
  width: 48px;
  height: 48px;
  align-self: flex-end;
}

.text {
  position: relative;
  max-width: 600px;
  width: 100%;
  margin-right: 15px;
  padding: 10px 15px;
  order: 1;
  font-family: "Montserrat", sans-serif;
  color: #fff;
  text-align: right;
  border-radius: 10px;
  background-color: #0066CC;
}

.text:after {
  position: absolute;
  content: '';
  right: -15px;
  bottom: 0px;
	border: 20px solid transparent; border-bottom: 20px solid #0066CC;
  z-index: -1;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.messageText {
  font-weight: 400;
  font-size: 16px;
}

.time {
  padding: 5px 10px 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 10px;
  color: #000;
}