.friendMessage {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 30px;
}

.friendImageAndText {
  display: flex;
}

.friendImageAndText img {
  width: 48px;
  height: 48px;
  align-self: flex-end;
}

.friendText {
  position: relative;
  max-width: 600px;
  width: 100%;
  min-height: 60px;
  margin-left: 15px;
  padding: 10px 15px;
  font-family: "Montserrat", sans-serif;
  color: #000;
  border-radius: 10px;
  background-color: #F5F7FB;
}

.friendText:after {
  position: absolute;
  content: '';
  left: -15px;
  bottom: 0px;
	border: 20px solid transparent; border-bottom: 20px solid #F5F7FB;
  z-index: -1;
}

.friendName {
  font-size: 18px;
  font-weight: 600;
}

.friendMessageText {
  font-weight: 400;
  font-size: 16px;
}

.friendTime {
  padding: 5px 10px 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 10px;
  color: #000;
}
