.sendForm { 
  display: flex;
  align-items: center;
}

.textarea {
  margin-right: 45px;
  padding: 24px 12px;
  border-radius: 20px;
  max-width: 976px;
  width: 100%;
  /* height: 40px !important; */
  box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
  background: #f5f7fb;
  overflow: hidden;
  outline: transparent;

  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);
  opacity: 0.9;
  border: none;
}

.button {
  max-width: 120px;
  width: 100%;
  border: none;
  border-radius: 20px;
  background: #06c;
  padding: 10px 38px;

  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
  cursor: pointer;

  transition: 0.2s background-color;
}

.button:hover {
  background-color: orange;
}

.button:active {
  background-color: rgb(119, 104, 86);
}
